// VoteDetailContent.tsxÏôÄ Í∞ôÏùÄ Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä Ïù¥ Í≤ΩÎ°úÏóêÏÑú useAuthÎ•º Í∞ÄÏ†∏Ïò§Í∏∞ ÎïåÎ¨∏Ïóê Î≥¥Ï°¥Ìï©ÎãàÎã§.

import { createBrowserSupabaseClient } from '@/lib/supabase/client';

// ÏÑ±Îä• ÏßÑÎã®ÏùÑ ÏúÑÌïú ÏßÅÏ†ëÏ†ÅÏù∏ getSession ÌÖåÏä§Ìä∏ Ìï®Ïàò (Î†àÍ±∞Ïãú)
// ‚ö†Ô∏è getSession()ÏùÄ ÎäêÎ¶ΩÎãàÎã§. Í∞ÄÎä•ÌïòÎ©¥ testGetUserPerformance()Î•º ÏÇ¨Ïö©ÌïòÏÑ∏Ïöî.
export async function testGetSessionPerformance() {
  console.log('üß™ [Performance Test] getSession ÏÑ±Îä• ÌÖåÏä§Ìä∏ ÏãúÏûë (Î†àÍ±∞Ïãú Î∞©Ïãù)');
  
  const supabase = createBrowserSupabaseClient();
  const testResults = {
    attempts: 3,
    results: [] as Array<{
      attempt: number;
      duration: number;
      success: boolean;
      error?: string;
    }>,
    average: 0,
    fastest: 0,
    slowest: 0
  };

  for (let i = 1; i <= testResults.attempts; i++) {
    const startTime = performance.now();
    
    try {
      console.log(`üèÉ [Performance Test] ÏãúÎèÑ ${i}/${testResults.attempts} ÏãúÏûë`);
      
      const result = await supabase.auth.getSession();
      const endTime = performance.now();
      const duration = endTime - startTime;
      
      testResults.results.push({
        attempt: i,
        duration,
        success: !result.error && !!result.data,
        error: result.error?.message
      });
      
      console.log(`‚úÖ [Performance Test] ÏãúÎèÑ ${i} ÏôÑÎ£å: ${duration.toFixed(2)}ms`);
      
      // ÏãúÎèÑ Í∞Ñ Í∞ÑÍ≤©
      if (i < testResults.attempts) {
        await new Promise(resolve => setTimeout(resolve, 100));
      }
      
    } catch (error) {
      const endTime = performance.now();
      const duration = endTime - startTime;
      
      testResults.results.push({
        attempt: i,
        duration,
        success: false,
        error: error instanceof Error ? error.message : 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'
      });
      
      console.log(`‚ùå [Performance Test] ÏãúÎèÑ ${i} Ïã§Ìå®: ${duration.toFixed(2)}ms - ${error}`);
    }
  }

  // Í≤∞Í≥º Î∂ÑÏÑù
  const durations = testResults.results.map(r => r.duration);
  testResults.average = durations.reduce((a, b) => a + b, 0) / durations.length;
  testResults.fastest = Math.min(...durations);
  testResults.slowest = Math.max(...durations);
  
  console.log('üìä [Performance Test] ÏµúÏ¢Ö Í≤∞Í≥º:');
  console.table(testResults.results);
  console.log(`‚ö° ÌèâÍ∑†: ${testResults.average.toFixed(2)}ms`);
  console.log(`üèÜ ÏµúÍ≥† ÏÜçÎèÑ: ${testResults.fastest.toFixed(2)}ms`);
  console.log(`üêå ÏµúÏ†Ä ÏÜçÎèÑ: ${testResults.slowest.toFixed(2)}ms`);
  
  return testResults;
}

