if (typeof window !== 'undefined') {
  (window as any).testSupabasePerformance = testGetSessionPerformance; // Î†àÍ±∞Ïãú Ìò∏ÌôòÏÑ±
  (window as any).testGetSessionPerformance = testGetSessionPerformance;
  (window as any).testGetUserPerformance = testGetUserPerformance; 
  (window as any).compareSupabasePerformance = compareGetSessionVsGetUser;
  (window as any).diagnoseSupabaseInfiniteWait = diagnoseSupabaseInfiniteWait;
  
  console.log('üõ†Ô∏è [useAuth] ÏßÑÎã® Ìï®ÏàòÎì§Ïù¥ Ï†ÑÏó≠ÏúºÎ°ú Îì±Î°ùÎêòÏóàÏäµÎãàÎã§:');
  console.log('  - testGetSessionPerformance() : getSession ÌÖåÏä§Ìä∏ (Î†àÍ±∞Ïãú)');
  console.log('  - testGetUserPerformance() : getUser ÌÖåÏä§Ìä∏ (Í∂åÏû• ‚≠ê)');
  console.log('  - compareSupabasePerformance() : ÏÑ±Îä• ÎπÑÍµê');
  console.log('  - diagnoseSupabaseInfiniteWait() : Î¨¥ÌïúÎåÄÍ∏∞ Í∑ºÎ≥∏ ÏõêÏù∏ Î∂ÑÏÑù');
}
